import React, { useState } from "react";
import Container from "../Common/Container/Container";

// NFT IMAGES
import NFTItems from "../NFTItems/NFTItems";
import { ToolNFTData, ToolTokenData, ToolADAData } from "./NFTData";
import TabNavItem from "../ReactTabs/TabNavItem";
import TabContent from "../ReactTabs/TabContent";
import SearchBar from "../Common/SearchBar/SearchBar";
import ToolHoldingPortfolio from "../ToolHoldingPortfolio/ToolHoldingPortfolio";
import SoldPortfolio from "../SoldPortfolio/SoldPortfolio";

const ToolNFT = () => {
  const [activeTab, setActiveTab] = useState("NFTs");
  return (
    <>
      <div className="!my-28"></div>
      <Container>
        <div className="my-10">
          <div className="flex justify-center flex-wrap gap-4 my-10">
            <TabNavItem
              title="Holding Portfolio"
              id="NFTs"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
            <TabNavItem
              title="Sold Portfolio"
              id="tokens"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
          </div>

          <SearchBar />

          <div className="flex gap-4 flex-wrap items-center mt-5 text-xl font-bold">
            <TabNavItem
              title="NFTs"
              id="NFTs"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
            <TabNavItem
              title="Tokens"
              id="tokens"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
            <TabNavItem
              title="ADA"
              id="ada"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
            <TabNavItem
              title="Select Multiple"
              id="mul"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
            <TabNavItem
              title="Select all Collections"
              id="collections"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
            <TabNavItem
              title="Select Multiple Projects"
              id="mulproject"
              activeTab={activeTab}
              setActiveTab={setActiveTab}
            />
          </div>

          <TabContent id="NFTs" activeTab={activeTab}>
            <div className="grid md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 place-items-center my-10">
              {ToolNFTData?.map((x, index) => (
                <div key={index}>
                  <NFTItems item={x} />
                </div>
              ))}
            </div>
          </TabContent>
          <TabContent id="tokens" activeTab={activeTab}>
            <div className="grid md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 place-items-center my-10">
              {ToolTokenData?.map((x, index) => (
                <div key={index}>
                  <NFTItems item={x} />
                </div>
              ))}
            </div>
          </TabContent>
          <TabContent id="ada" activeTab={activeTab}>
            <div className="grid md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 place-items-center my-10">
              {ToolADAData?.map((x, index) => (
                <div key={index}>
                  <NFTItems item={x} />
                </div>
              ))}
            </div>
          </TabContent>
        </div>

        {/* ToolHolding */}
        <ToolHoldingPortfolio />
      </Container>

      {/* <NFTtable /> */}
      <SoldPortfolio />
    </>
  );
};

export default ToolNFT;
