import React from "react";
import Frame from "../Frame/Frame";

const SearchBar = () => {
  return (
    <>
      <Frame className="min-w-md lg:w-full bg-[#1c1c1c]">
        <div className="w-full">
          <div className="relative items-center flex w-full rounded-lg  overflow-hidden">
            <div className="text-[#ABABAB]">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                />
              </svg>
            </div>
            <input
              className="peer font-helvetica w-full h-full  outline-none text-[#ABABAB] pl-6 bg-[#1c1c1c] text-2xl"
              type="text"
              id="search"
              placeholder="Search bar"
            />
          </div>
        </div>
      </Frame>
    </>
  );
};

export default SearchBar;
